import React, { useState, useCallback } from "react";
import styled from "styled-components";
import { Modal } from "react-bootstrap";
import SignIn from "./SignInContainer";
import SignUp from "./SignUpContainer";
import { StateChanger } from "../Form";
import { useDispatch } from "../../context";
import { HIDE_LOGIN_MODAL } from "../../context/action";

const Link = styled.span`
    color: ${(props) => props.theme.blueColor};
    cursor: pointer;
`;

/**
 * *   
 *
 * @Component
 * @Modal
 * @author frisk
 */
const Auth = () => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *     
     */
    const [action, setAction] = useState("login");
    /**
     *   
     */
    const handleClose = useCallback(() => {
        dispatch({
            type: HIDE_LOGIN_MODAL
        });
    }, []);

    return (
        <Modal onHide={handleClose} show animation={false}>
            <Modal.Header closeButton>
                <Modal.Title>
                    {action === "login" ? "" : ""}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {action === "signup" ? (
                    <SignUp setAction={setAction} />
                ) : (
                    <SignIn />
                )}
                <StateChanger>
                    {action === "login" && (
                        <div>
                             &nbsp;
                            <Link onClick={() => setAction("signup")}>
                                
                            </Link>
                        </div>
                    )}
                    {action === "signup" && (
                        <div>
                             &nbsp;
                            <Link onClick={() => setAction("login")}>
                                
                            </Link>
                        </div>
                    )}
                </StateChanger>
            </Modal.Body>
        </Modal>
    );
};

export default Auth;
