import React, { useReducer, useContext, createContext } from "react";
import reducer from "./reducer";

const Context = createContext(null);

const DispatchContext = createContext(null);

/**
 * *  
 *
 * @author frisk
 * @property id                 -  ID
 * @property nickname           -  
 * @property email              -  
 * @property avatar             -   
 * @property isMaster           -  
 * @property isShowNoticeModal  -    
 * @property isShowAddPostModal -     
 * @property isShowSearchBar    -   
 * @property isShowFilterBar    -    
 * @property isShowLoginModal   -    
 * @property activePost         -   
 * @property activeNotice       -   
 * @property searchPostOption   -  
 * @property isCollapseNav      -   (expand, contract)
 * @property isMobile          -   
 */
const initialState = {
    id: null,
    nickname: null,
    email: null,
    avatar: null,
    isMaster: false,
    isShowNoticeModal: false,
    isShowAddPostModal: false,
    isShowSearchBar: false,
    isShowFilterBar: false,
    isShowLoginModal: false,
    isCollapseNav: "expand",
    isMobile: false,
    activePost: {
        id: "",
        title: "",
        description: ""
    },
    activeNotice: {
        id: "",
        action: "wait",
        actionText: "",
        title: "",
        description: ""
    },
    feedPostOption: {},
    searchPostOption: {
        first: 30,
        orderBy: "createdAt_DESC",
        query: undefined,
        category: undefined,
        userId: undefined
    }
};

/**
 * *    
 */
export function ContextProvider({ children }) {
    const [state, dispatch] = useReducer(reducer, initialState);

    return (
        <Context.Provider value={state}>
            <DispatchContext.Provider value={dispatch}>
                {children}
            </DispatchContext.Provider>
        </Context.Provider>
    );
}

/**
 * * Hooks -    
 */
export function useSelector() {
    const state = useContext(Context);

    if (!state) {
        throw new Error("Provider is not defined");
    }

    return state;
}

/**
 * * Hooks -    
 */
export function useDispatch() {
    const dispatch = useContext(DispatchContext);

    if (!dispatch) {
        throw new Error("Provider is not defined");
    }

    return dispatch;
}
