import { gql } from "@apollo/client";

/**
 * *  
 *
 * @query
 * @author frisk
 * @param $skip   
 * @param $first   
 * @param $orderBy 
 */
export const GET_CATEGORIES = gql`
    query GetCategories($skip: Int, $first: Int, $orderBy: String) {
        categories(skip: $skip, first: $first, orderBy: $orderBy) {
            id
            content
            useCount
        }
    }
`;
