import React, { useCallback } from "react";
import { Profile, Logout } from "../assets/icon";
import { useDispatch, useSelector } from "../context";
import { SHOW_LOGIN_MODAL, SET_ME } from "../context/action";
import { TOKEN_KEY, getStorage, deleteStorage } from "../lib/state";
import Avatar from "./Avatar";

/**
 *    
 *
 * @Component
 * @author frisk
 */
const ProfileBtn = () => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *     
     */
    const { id, avatar } = useSelector();
    /**
     *  
     */
    const handleClick = useCallback(() => {
        /**
         *  
         */
        const token = getStorage(TOKEN_KEY);
        if (token) {
            const tf = window.confirm(" ?");
            if (tf) {
                /**
                 *  
                 */
                deleteStorage(TOKEN_KEY);
                /**
                 *   
                 */
                dispatch({
                    type: SET_ME,
                    id: null,
                    nickname: null,
                    email: null,
                    avatar: null,
                    isMaster: false
                });
            }
        } else {
            /**
             *   
             */
            dispatch({
                type: SHOW_LOGIN_MODAL
            });
        }
    }, []);

    return (
        <div>
            {id ? (
                <div className="d-flex justify-content-start">
                    <Avatar src={avatar.url} size="30" userId={id} />
                    <button
                        type="button"
                        onClick={handleClick}
                        className="ml-2"
                    >
                        <Logout />
                        <span className="a11y-hidden"> </span>
                    </button>
                </div>
            ) : (
                <button
                    type="button"
                    onClick={handleClick}
                    aria-haspopup="true"
                >
                    <Profile />
                    <span className="a11y-hidden"> </span>
                </button>
            )}
        </div>
    );
};

export default ProfileBtn;
