import { gql } from "@apollo/client";

/**
 * *  
 *
 * @query
 * @author frisk
 * @param $skip   
 * @param $first   
 * @param $orderBy 
 * @param $postId  ID
 */
export const GET_COMMENTS = gql`
    query GetComments(
        $skip: Int
        $first: Int
        $orderBy: String
        $postId: String
    ) {
        comments(
            skip: $skip
            first: $first
            orderBy: $orderBy
            postId: $postId
        ) {
            data {
                id
                user {
                    id
                    nickname
                    avatar {
                        url
                    }
                }
                post {
                    id
                    commentCount
                }
                content
                createdAt
                updatedAt
            }
            total
        }
    }
`;
