import { gql } from "@apollo/client";

/**
 * *  
 *
 * @mutation
 * @author frisk
 * @param $title 
 * @param $description 
 * @param $content 
 * @param $category 
 * @param $thumbnail 
 */
export const CREATE_POST = gql`
    mutation createPost(
        $title: String!
        $description: String!
        $content: String!
        $category: String!
        $thumbnail: String
    ) {
        createPost(
            title: $title
            description: $description
            content: $content
            category: $category
            thumbnail: $thumbnail
        )
    }
`;

/**
 * *  
 *
 * @mutation
 * @author frisk
 * @param $id  ID
 * @param $title 
 * @param $description 
 * @param $content 
 * @param $category 
 * @param $thumbnail 
 */
export const UPDATE_POST = gql`
    mutation updatePost(
        $id: String!
        $title: String!
        $description: String!
        $content: String!
        $category: String!
        $thumbnail: String
    ) {
        updatePost(
            id: $id
            title: $title
            description: $description
            content: $content
            category: $category
            thumbnail: $thumbnail
        )
    }
`;

/**
 * *  
 *
 * @mutation
 * @author frisk
 * @param $id  ID
 */
export const DELETE_POST = gql`
    mutation deletePost($id: String!) {
        deletePost(id: $id)
    }
`;

/**
 * *   /  
 *
 * @mutation
 * @author frisk
 * @param $id  ID
 */
export const LIKE_POST = gql`
    mutation likePost($id: String!) {
        likePost(id: $id)
    }
`;
