import { gql } from "@apollo/client";

/**
 * *  
 *
 * @query
 * @author frisk
 * @param $skip   
 * @param $first   
 * @param $orderBy 
 * @param $query 
 */
export const GET_USERS = gql`
    query GetUsers($skip: Int, $first: Int, $orderBy: String) {
        users(skip: $skip, first: $first, orderBy: $orderBy) {
            id
            nickname
            isMaster
            createdAt
            updatedAt
            postCount
            avatar {
                url
            }
        }
    }
`;

/**
 * *   
 *
 * @query
 * @author frisk
 * @param $id  ID
 */
export const GET_USER = gql`
    query GetUser($id: String!) {
        user(id: $id) {
            id
            nickname
            isMaster
            createdAt
            updatedAt
            postCount
            avatar {
                url
            }
        }
    }
`;

/**
 * *  
 *
 * @query
 * @author frisk
 */
export const ME = gql`
    query Me {
        me {
            id
            nickname
            email
            avatar {
                url
            }
            isMaster
        }
    }
`;
