import { gql } from "@apollo/client";

/**
 * *  
 *
 * @query
 * @author frisk
 * @param $skip   
 * @param $first   
 * @param $orderBy 
 */
export const GET_NOTICES = gql`
    query GetNotices($skip: Int, $first: Int, $orderBy: String) {
        notices(skip: $skip, first: $first, orderBy: $orderBy) {
            id
            title
            description
            createdAt
            updatedAt
        }
    }
`;
