import React, { useCallback, FormEvent, FC } from "react";
import { useMutation } from "@apollo/client";
import { useInput } from "../../hooks";
import { SIGN_IN } from "../../graphql/mutation/user";
import SignInPresenter from "./SignInPresenter";
import { TOKEN_KEY, setStorage } from "../../lib/state";
import { useDispatch } from "../../context";
import { SET_ME, HIDE_LOGIN_MODAL } from "../../context/action";

/**
 * *   
 *
 * @Container
 * @author frisk
 */
const SignInContainer = () => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *  mutation 
     */
    const [login, { loading }] = useMutation(SIGN_IN);
    /**
     *    useInput 
     */
    const email = useInput("");
    /**
     *   
     */
    const handleSubmit = useCallback(
        async (e) => {
            e.preventDefault();
            /**
             *   
             */
            if (loading) {
                return alert(" .  .");
            }

            try {
                const {
                    data: { logIn }
                } = await login({
                    variables: { email: email.value }
                });
                if (logIn) {
                    const {
                        token,
                        id,
                        nickname,
                        email,
                        avatar,
                        isMaster
                    } = logIn;
                    /**
                     *  
                     */
                    setStorage(TOKEN_KEY, token);
                    /**
                     *     
                     */
                    dispatch({
                        type: SET_ME,
                        id,
                        nickname,
                        email,
                        avatar,
                        isMaster
                    });
                    /**
                     *   
                     */
                    dispatch({
                        type: HIDE_LOGIN_MODAL
                    });
                }
            } catch (error) {
                const { message } = JSON.parse(error.message);
                alert(message);
            }
        },
        [email.value, loading]
    );

    return (
        <SignInPresenter
            loading={loading}
            email={email}
            onSubmit={handleSubmit}
        />
    );
};

export default SignInContainer;
