import React from "react";
import styled from "styled-components";
import { FormInput } from "../Form";
import Button from "../Button";
import Loader from "../Loader";
import { Thumbnail } from "../../assets/icon";

const UploadWrapper = styled.div`
    ${(props) => props.theme.whiteBox}
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;

    & img {
        width: 100%;
        height: 100%;
    }
`;

/**
 * *   
 *
 * @Presenter
 * @author frisk
 * @param props.uploadLoading    
 * @param props.signUpLoading    
 * @param props.nickname    Hooks
 * @param props.email    Hooks
 * @param props.preview  
 * @param props.$file file element
 * @param props.onChangeFile   
 * @param props.onClickFile   
 * @param props.onSubmit   
 */
const SignUpPresenter = ({
    uploadLoading,
    signUpLoading,
    nickname,
    email,
    preview,
    $file,
    onChangeFile,
    onClickFile,
    onSubmit
}) => {
    return (
        <>
            {(uploadLoading || signUpLoading) && <Loader />}
            <form onSubmit={onSubmit}>
                <UploadWrapper onClick={onClickFile} role="button" tabIndex="0">
                    {preview ? (
                        <img
                            src={preview}
                            alt="avatar"
                            title=" ."
                        />
                    ) : (
                        <Thumbnail style={{ width: 100, height: 50 }} />
                    )}

                    <input
                        type="file"
                        onChange={onChangeFile}
                        ref={$file}
                        hidden
                        accept="image/jpg, image/jpeg, image/png, .gif"
                    />
                </UploadWrapper>
                <FormInput
                    type="email"
                    placeholder=" ."
                    name="email"
                    autoComplete="off"
                    required
                    {...email}
                    label=""
                />
                <FormInput
                    placeholder=" "
                    required
                    name="nickname"
                    {...nickname}
                    autoComplete="off"
                    label=""
                />
                <Button type="submit"></Button>
            </form>
        </>
    );
};

export default SignUpPresenter;
