import React, { FC, useState, useCallback, memo } from "react";
import styled from "styled-components";
import { useMutation } from "@apollo/client";
import { Dropdown } from "react-bootstrap";
import { UPDATE_COMMENT, DELETE_COMMENT } from "../graphql/mutation/comment";
import { useInput } from "../hooks";
import { FormTextArea } from "./Form";
import Avatar from "./Avatar";
import Button from "./Button";
import { TOKEN_KEY, getStorage } from "../lib/state";
import { useDispatch } from "../context";
import { SHOW_LOGIN_MODAL } from "../context/action";
import { useSelector } from "../context";
import { More } from "../assets/icon";
import Loader from "./Loader";
import { timeForToday } from "../lib/date";

const CommentWrapper = styled.div`
    display: flex;
    flex-direction: column;
    border-top: ${(props) => props.theme.boxBorder};
    padding-top: 1rem;
    padding-bottom: 1rem;
`;

const InfoWrapper = styled.div`
    margin-bottom: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & > * {
        margin-right: 10px;
    }
`;

const BtnWrapper = styled.div`
    display: flex;
    justify-content: space-between;

    & > button {
        flex: 1;
    }

    & > button:nth-child(1) {
        background: ${(props) => props.theme.infoColor};
        border: 1px solid ${(props) => props.theme.infoColor};
    }
`;

const OptionWrapper = styled.div`
    flex: 1;
    text-align: right;
    position: relative;
`;

const MoreWrapper = styled.div`
    position: relative;
    & #dropdown-custom-2 {
        position: absolute;
        top: -20px;
        right: 0;
        opacity: 0;
        z-index: 1;
    }

    & svg {
        fill: gray;
        cursor: pointer;
        position: absolute;
        top: -10px;
        right: -5px;
    }
`;

/**
 *   
 *
 * @Component
 * @author frisk
 */
const CommentItem = ({ id, content, user, createdAt }) => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *     
     */
    const { id: userId } = useSelector();
    /**
     *   mutation 
     */
    const [upd, { loading: updateLoading }] = useMutation(UPDATE_COMMENT);
    /**
     *   mutation 
     */
    const [del, { loading: deleteLoading }] = useMutation(DELETE_COMMENT);
    /**
     *    useInput 
     */
    const comment = useInput(content);
    /**
     *      
     */
    const [active, setActive] = useState(false);
    /**
     *      
     */
    const [contentState, setContentState] = useState(content);
    /**
     *     
     */
    const [disabled, setDisabled] = useState(false);
    /**
     *    
     */
    const isMyComment = userId ? userId === user.id : false;
    /**
     *    
     */
    const handleActive = useCallback(() => {
        setActive(true);
    }, []);
    /**
     *  
     */
    const handleCancel = useCallback(() => {
        const tf = confirm(" ?");
        if (tf) {
            setActive(false);
        }
    }, []);
    /**
     *   
     */
    const handleUpdate = useCallback(async () => {
        /**
         *   
         */
        if (updateLoading) {
            return alert(" .  .");
        }

        /**
         *  
         */
        const token = getStorage(TOKEN_KEY);

        if (token) {
            if (comment.value.length > 100) {
                return alert(" 100   .");
            }
            const tf = confirm("  ?");

            if (tf) {
                try {
                    await upd({
                        variables: { id, content: comment.value }
                    });
                    /**
                     *    
                     */
                    setContentState(comment.value);
                    /**
                     *   
                     */
                    setActive(false);
                } catch (error) {
                    const { message, status } = JSON.parse(error.message);
                    if (status === 401) {
                        /**
                         *   
                         */
                        dispatch({
                            type: SHOW_LOGIN_MODAL
                        });
                    } else {
                        alert(message);
                    }
                }
            }
        } else {
            /**
             *   
             */
            dispatch({
                type: SHOW_LOGIN_MODAL
            });
        }
    }, [updateLoading, comment.value]);
    /**
     *   
     */
    const handleDelete = useCallback(async () => {
        /**
         *   
         */
        if (deleteLoading) {
            return alert(" .  .");
        }

        const tf = confirm(" ?");

        if (tf) {
            try {
                await del({
                    variables: { id }
                });
                /**
                 *  
                 */
                setDisabled(true);
                /**
                 *   
                 */
                setActive(false);
            } catch (error) {
                const { message } = JSON.parse(error.message);
                alert(message);
            }
        }
    }, []);

    return (
        <CommentWrapper>
            {(updateLoading || deleteLoading) && <Loader />}
            <InfoWrapper>
                <Avatar src={user.avatar.url} size="50" userId={user.id} />
                <span>{user.nickname}</span>
                <span></span>
                <span>{timeForToday(createdAt)}</span>
                {isMyComment && (
                    <OptionWrapper>
                        <MoreWrapper>
                            <Dropdown>
                                <Dropdown.Toggle id="dropdown-custom-2" />
                                {!disabled && <More />}
                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={handleActive}>
                                        
                                    </Dropdown.Item>
                                    <Dropdown.Divider />
                                    <Dropdown.Item onClick={handleDelete}>
                                        
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </MoreWrapper>
                    </OptionWrapper>
                )}
            </InfoWrapper>
            {active ? (
                <>
                    <FormTextArea
                        placeholder=" ."
                        name="comment"
                        autoComplete="off"
                        height={100}
                        label=""
                        {...comment}
                    />
                    <BtnWrapper>
                        <Button onClick={handleCancel}></Button>
                        <Button onClick={handleUpdate}> </Button>
                    </BtnWrapper>
                </>
            ) : (
                <pre>
                    {disabled ? <em> .</em> : contentState}
                </pre>
            )}
        </CommentWrapper>
    );
};

export default memo(CommentItem);
