import React from "react";
import styled from "styled-components";
import { GET_POSTS } from "../graphql/query/post";
import Query from "./Query";
import Scroll from "./Scroll";
import PostListTypeItem from "./PostListTypeItem";
import PostCardTypeItem from "./PostCardTypeItem";
import NoData from "./NoData";
import { useSelector } from "../context";

const Container = styled.div`
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    flex: 0 1 auto;
`;
/**
 * *    
 *
 * @Component
 * @author frisk
 * @param {string}            renderType      
 * @param {number|undefined}  first           
 * @param {string|undefined}  orderBy       
 * @param {string|undefined}  query         
 * @param {string|undefined}  category      
 * @param {string|undefined}  userId         ID
 */
const PostList = ({
    renderType,
    first = 30,
    orderBy = "createdAt_DESC",
    query,
    category,
    userId,
    children
}) => {
    /**
     *     
     */
    const { isMobile } = useSelector();

    return (
        <Query
            query={GET_POSTS}
            variables={{
                first,
                orderBy,
                query,
                category,
                userId
            }}
            notifyOnNetworkStatusChange={true}
        >
            {({ data: { posts }, loading, fetchMore }) =>
                children({
                    total: posts.total,
                    posts: (
                        <Container>
                            {posts.data.length > 0 ? (
                                <>
                                    {posts.data.map((post) => {
                                        if (isMobile) {
                                            return (
                                                <PostCardTypeItem
                                                    key={post.id}
                                                    {...post}
                                                />
                                            );
                                        } else {
                                            return (
                                                <PostListTypeItem
                                                    key={post.id}
                                                    renderType={renderType}
                                                    {...post}
                                                />
                                            );
                                        }
                                    })}
                                    <Scroll
                                        loading={loading}
                                        onBottom={() => {
                                            if (
                                                posts.data.length > 0 &&
                                                posts.data.length % first !== 0
                                            ) {
                                                return;
                                            }

                                            fetchMore({
                                                variables: {
                                                    first,
                                                    orderBy,
                                                    query,
                                                    category,
                                                    userId,
                                                    notNullThumb,
                                                    skip: posts.data.length
                                                },
                                                updateQuery: (
                                                    prev,
                                                    { fetchMoreResult }
                                                ) => {
                                                    if (!fetchMoreResult) {
                                                        return prev;
                                                    }

                                                    return {
                                                        posts: {
                                                            data: [
                                                                ...prev.posts
                                                                    .data,
                                                                ...fetchMoreResult
                                                                    .posts.data
                                                            ],
                                                            total: posts.total
                                                        }
                                                    };
                                                }
                                            });
                                        }}
                                    />
                                </>
                            ) : (
                                <NoData />
                            )}
                        </Container>
                    )
                })
            }
        </Query>
    );
};

export default PostList;
