import React from "react";
import styled from "styled-components";
import { Modal, Button } from "react-bootstrap";
import { FormInput, FormTextArea } from "../Form";
import Loader from "../Loader";

const ReadOnlyDescription = styled.div`
    ${(props) => props.theme.whiteBox};
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 300px;
    overflow: auto;
    background: white;
    padding: 15px;
`;

const PreviewWrap = styled(ReadOnlyDescription)`
    z-index: 10;

    & > span {
        position: absolute;
        top: 5px;
        right: 5px;
        cursor: pointer;
    }
`;

/**
 *    
 *
 * @Presenter
 * @author frisk
 * @param props.setNoticeLoading      
 * @param props.removeNoticeLoading    
 * @param props.action  
 * @param props.isMaster  
 * @param props.title    Hooks
 * @param props.description    Hooks
 * @param props.mdDescription  
 * @param props.preview 
 * @param props.onShowEdit    
 * @param props.onPreview  
 * @param props.onClosePreview   
 * @param props.onClose   
 * @param props.onDelete   
 * @param props.onSubmit     
 */
const SetNoticePresenter = ({
    setNoticeLoading,
    removeNoticeLoading,
    action,
    isMaster,
    title,
    description,
    mdDescription,
    preview,
    onShowEdit,
    onPreview,
    onClosePreview,
    onClose,
    onDelete,
    onSubmit
}) => (
    <Modal onHide={onClose} show animation={false}>
        {(setNoticeLoading || removeNoticeLoading) && <Loader />}
        <Modal.Header closeButton>
            <Modal.Title>
                {action.code === "readonly" || action.code === "modifiable"
                    ? title.value
                    : ` ${action.modalTitle}`}
            </Modal.Title>
        </Modal.Header>
        <form onSubmit={onSubmit}>
            <Modal.Body>
                {(action.code === "add" || action.code === "modify") && (
                    <FormInput
                        placeholder=" ."
                        name="title"
                        required
                        autoComplete="off"
                        {...title}
                        label=""
                    />
                )}
                <FormTextArea
                    placeholder=" ."
                    name="description"
                    required
                    autoComplete="off"
                    height={300}
                    {...description}
                    label=""
                >
                    {(action.code === "readonly" ||
                        action.code === "modifiable") && (
                        <ReadOnlyDescription
                            dangerouslySetInnerHTML={{ __html: mdDescription }}
                            className="markdown-body"
                        ></ReadOnlyDescription>
                    )}
                    {preview && (
                        <PreviewWrap>
                            <div
                                dangerouslySetInnerHTML={{ __html: preview }}
                                className="markdown-body"
                            ></div>
                            <span aria-hidden="true" onClick={onClosePreview}>
                                
                            </span>
                        </PreviewWrap>
                    )}
                </FormTextArea>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onClose}>
                    
                </Button>
                {(action.code === "readonly" ||
                    action.code === "modifiable") && (
                    <>
                        {isMaster && (
                            <Button variant="danger" onClick={onDelete}>
                                
                            </Button>
                        )}
                        <Button
                            variant="primary"
                            onClick={isMaster ? onShowEdit : onClose}
                        >
                            {isMaster ? "" : ""}
                        </Button>
                    </>
                )}
                {(action.code === "modify" || action.code === "add") && (
                    <>
                        <Button
                            variant="info"
                            onClick={preview ? onClosePreview : onPreview}
                        >
                            {preview ? " " : ""}
                        </Button>
                        <Button variant="primary" type="submit">
                            {action.code === "add" ? "" : ""}
                        </Button>
                    </>
                )}
            </Modal.Footer>
        </form>
    </Modal>
);

export default SetNoticePresenter;
