import React, { useState, useCallback } from "react";
import { useMutation } from "@apollo/client";
import styled from "styled-components";
import { CREATE_POST } from "../../graphql/mutation/post";
import { useDispatch } from "../../context";
import { SHOW_LOGIN_MODAL } from "../../context/action";
import Editor from "../../components/Editor";
import { FormInput } from "../../components/Form";
import Button from "../../components/Button";
import { useInput } from "../../hooks";
import Loader from "../../components/Loader";
import { TOKEN_KEY, getStorage } from "../../lib/state";

const Container = styled.form`
    & input {
        background: white;
    }
`;

const CategoryWrapper = styled.div`
    height: auto;
    display: flex;
    justify-content: flex-start;
`;

const SubmitWrapper = styled.div`
    margin-top: 1rem;
`;

/**
 *    
 *
 * @Page
 * @author frist
 */
const CreatePostPage = () => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *   mutation 
     */
    const [create, { loading }] = useMutation(CREATE_POST);
    /**
     *    useInput 
     */
    const title = useInput("");
    /**
     *    useInput 
     */
    const category = useInput("", "no_space");
    /**
     *     
     */
    const [content, setContent] = useState("");
    /**
     *  
     */
    const handleSubmit = useCallback(
        async (e) => {
            e.preventDefault();
            /**
             *  
             */
            const token = getStorage(TOKEN_KEY);

            if (token) {
                /**
                 *    
                 */
                if (loading) {
                    return alert(" ");
                }
                if (title.value.length > 50) {
                    return alert(" 50  .");
                }
                if (category.value.length > 10) {
                    return alert(" 10  .");
                }
                /**
                 * , 
                 * @type {string|undefined}
                 */
                let thumbnail, description;
                /**
                 *   
                 */
                const reg = /\!\[([^\]]+)\]\(([^\)]+)\)/g;

                let foundThumbnails = content.match(reg);

                if (foundThumbnails) {
                    thumbnail = foundThumbnails[0].substring(
                        foundThumbnails[0].indexOf("(") + 1,
                        foundThumbnails[0].lastIndexOf(")")
                    );
                    description = content.replace(reg, "");
                } else {
                    description = content;
                }
                /**
                 *   
                 */
                const reg2 = /[\{\}\[\]\/?.,;:|\)*~`!^\-_+<>@\#$%&\\\=\(\'\"]/gi;

                description = description.replace(reg2, "");

                const tf = confirm("   ?");

                if (tf) {
                    try {
                        const {
                            data: { createPost }
                        } = await create({
                            variables: {
                                title: title.value,
                                description,
                                content,
                                category: category.value,
                                thumbnail
                            }
                        });
                        if (createPost) {
                            alert(" .");
                        }
                    } catch (error) {
                        const { message, status } = JSON.parse(error.message);
                        if (status === 401) {
                            /**
                             *   
                             */
                            dispatch({
                                type: SHOW_LOGIN_MODAL
                            });
                        } else {
                            alert(message);
                        }
                    }
                }
            } else {
                /**
                 *   
                 */
                dispatch({
                    type: SHOW_LOGIN_MODAL
                });
            }
        },
        [title.value, category.value, content]
    );

    return (
        <Container onSubmit={handleSubmit}>
            {loading && <Loader />}
            <CategoryWrapper>
                <FormInput
                    type="text"
                    placeholder=" "
                    name="category"
                    autoComplete="off"
                    required
                    {...category}
                    label=""
                />
            </CategoryWrapper>
            <FormInput
                type="text"
                placeholder=" "
                name="title"
                autoComplete="off"
                required
                {...title}
                label=""
            />
            <Editor onChange={(content) => setContent(content)} />
            <SubmitWrapper>
                <Button type="submit"></Button>
            </SubmitWrapper>
        </Container>
    );
};

export default CreatePostPage;
