import { gql } from "@apollo/client";

/**
 * *  
 *
 * @query
 * @author frisk
 * @param $skip             
 * @param $first            
 * @param $orderBy        
 * @param $query          
 * @param $category       
 * @param $userId          ID
 * @param $notNullThumb       
 */
export const GET_POSTS = gql`
    query GetPosts(
        $skip: Int
        $first: Int
        $orderBy: String
        $query: String
        $category: String
        $userId: String
        $notNullThumb: Boolean
    ) {
        posts(
            skip: $skip
            first: $first
            orderBy: $orderBy
            query: $query
            category: $category
            userId: $userId
            notNullThumb: $notNullThumb
        ) {
            data {
                id
                title
                description
                user {
                    id
                    nickname
                    avatar {
                        url
                    }
                }
                likeCount
                likes {
                    id
                    user {
                        id
                    }
                }
                createdAt
                updatedAt
                viewCount
                commentCount
                category
                thumbnail
            }
            total
        }
    }
`;

/**
 * *   
 *
 * @query
 * @author frisk
 * @param $id   
 */
export const GET_POST = gql`
    query GetPost($id: String!) {
        post(id: $id) {
            id
            title
            description
            content
            user {
                id
                nickname
                avatar {
                    url
                }
            }
            likeCount
            likes {
                id
                user {
                    id
                }
            }
            createdAt
            updatedAt
            viewCount
            category
        }
    }
`;
