export const timeForToday = (value) => {
    const today = new Date();
    const timeValue = new Date(value);

    const betweenTime = Math.floor(
        (today.getTime() - timeValue.getTime()) / 1000 / 60
    );
    if (betweenTime < 1) return " ";
    if (betweenTime < 60) {
        return `${betweenTime} `;
    }

    const betweenTimeHour = Math.floor(betweenTime / 60);
    if (betweenTimeHour < 24) {
        return `${betweenTimeHour} `;
    }

    const betweenTimeDay = Math.floor(betweenTime / 60 / 24);
    if (betweenTimeDay < 31) {
        return `${betweenTimeDay} `;
    }

    const betweenTimeWeek = Math.floor(betweenTime / 60 / 24 / 7);
    if (betweenTimeWeek < 5) {
        return `${betweenTimeWeek} `;
    }

    const betweenTimeMonth = Math.floor(betweenTime / 60 / 24 / 7 / 12);
    if (betweenTimeMonth < 13) {
        return `${betweenTimeMonth} `;
    }

    return `${Math.floor(betweenTimeDay / 365)} `;
};
