import React, { useState, useCallback, useRef } from "react";
import { useMutation } from "@apollo/client";
import { useInput, useLazyAxios } from "../../hooks";
import SignUpPresenter from "./SignUpPresenter";
import { SIGN_UP } from "../../graphql/mutation/user";

/**
 * *   
 *
 * @Container
 * @author frisk
 * @param props.setAction     (, )
 */
const SignUpContainer = ({ setAction }) => {
    /**
     *    Axios 
     */
    const { loading: uploadLoading, call } = useLazyAxios();
    /**
     * file element
     */
    const $file = useRef(null);
    /**
     *    useInput 
     */
    const nickname = useInput("");
    /**
     *    useInput 
     */
    const email = useInput("");
    /**
     *     
     */
    const [preview, setPreview] = useState("");
    /**
     *    
     */
    const [file, setFile] = useState("");
    /**
     *  mutation 
     */
    const [signUp, { loading: signUpLoading }] = useMutation(SIGN_UP);
    /**
     *   
     */
    const handleChangeFile = useCallback(
        async (e) => {
            const { value, files } = e.target;
            /**
             *    
             */
            if (!value) {
                return;
            }
            /**
             *    
             */
            if (uploadLoading) {
                return;
            }

            const [file] = files;

            const formData = new FormData();
            formData.append("file", file);

            const { data, error } = await call({
                method: "post",
                url: `${process.env.BACKEND_API_PATH}/api/upload`,
                data: formData,
                headers: { "content-type": "multipart/form-data" }
            });

            if (data) {
                const reader = new FileReader();

                reader.onloadend = () => {
                    /**
                     *   
                     */
                    setPreview(reader.result);
                    /**
                     *    URL 
                     */
                    setFile(data);
                };
                /**
                 * base64 
                 */
                reader.readAsDataURL(file);
            }

            if (error) {
                alert("    .");
            }
        },
        [uploadLoading]
    );
    /**
     *   
     */
    const handleClickFile = useCallback(() => {
        const node = $file.current;
        if (node) {
            node.click();
        }
    }, []);
    /**
     *   
     */
    const handleSubmit = useCallback(
        async (e) => {
            e.preventDefault();
            /**
             *    
             */
            if (signUpLoading) {
                return alert(" .  .");
            }

            const tf = confirm("   ?");

            if (tf) {
                try {
                    const {
                        data: { createUser }
                    } = await signUp({
                        variables: {
                            email: email.value,
                            nickname: nickname.value,
                            file: file || process.env.RAZZLE_DEFAULT_AVATAR
                        }
                    });
                    if (createUser) {
                        /**
                         *   
                         */
                        setAction("login");
                        alert(" .");
                    }
                } catch (error) {
                    const { message } = JSON.parse(error.message);
                    alert(message);
                }
            }
        },
        [email.value, nickname.value, file, signUpLoading]
    );

    return (
        <SignUpPresenter
            uploadLoading={uploadLoading}
            signUpLoading={signUpLoading}
            nickname={nickname}
            email={email}
            preview={preview}
            $file={$file}
            onChangeFile={handleChangeFile}
            onClickFile={handleClickFile}
            onSubmit={handleSubmit}
        />
    );
};

export default SignUpContainer;
