import React, { useState, useCallback, useEffect } from "react";
import styled from "styled-components";
import { useQuery, useMutation } from "@apollo/client";
import { UPDATE_POST } from "../../graphql/mutation/post";
import { GET_POST } from "../../graphql/query/post";
import { useDispatch } from "../../context";
import { SHOW_LOGIN_MODAL } from "../../context/action";
import Editor from "../../components/Editor";
import Button from "../../components/Button";
import { useInput } from "../../hooks";
import Loader from "../../components/Loader";
import { TOKEN_KEY, getStorage } from "../../lib/state";
import { FormInput } from "../../components/Form";

const Container = styled.div`
    & input {
        background: white;
    }
`;

const CategoryWrapper = styled.div`
    height: auto;
    min-height: 50px;
    display: flex;
    justify-content: flex-start;
`;

const SubmitWrapper = styled.div`
    margin-top: 1rem;
`;

/**
 *    
 *
 * @Nextpage
 * @author frist
 */
const UpdatePostPage = ({ match }) => {
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *   
     */
    const { data } = useQuery(GET_POST, {
        variables: {
            id: match.params.id
        }
    });
    /**
     *   mutation 
     */
    const [upd, { loading: updatePostLoading }] = useMutation(UPDATE_POST);
    /**
     *    useInput 
     */
    const title = useInput("");
    /**
     *    useInput 
     */
    const category = useInput("", "no_space");
    /**
     *     
     */
    const [content, setContent] = useState("");
    /**
     *  
     */
    const handleSubmit = useCallback(async () => {
        /**
         *  
         */
        const token = getStorage(TOKEN_KEY);

        if (token) {
            /**
             *    
             */
            if (updatePostLoading) {
                return alert(" ");
            }
            if (!title.value) {
                return alert(" .");
            }
            if (title.value.length > 50) {
                return alert(" 50  .");
            }
            if (category.value.length > 10) {
                return alert(" 10  .");
            }
            /**
             * , 
             * @type {string|undefined}
             */
            let thumbnail, description;
            /**
             *   
             */
            const reg = /\!\[([^\]]+)\]\(([^\)]+)\)/g;

            let foundThumbnails = content.match(reg);

            if (foundThumbnails) {
                thumbnail = foundThumbnails[0].substring(
                    foundThumbnails[0].indexOf("(") + 1,
                    foundThumbnails[0].lastIndexOf(")")
                );
                description = content.replace(reg, "");
            } else {
                description = content;
            }
            /**
             *   
             */
            const reg2 = /[\{\}\[\]\/;:|\)*~`!^\-_+<>@\#$%&\\\=\(\'\"]/gi;

            description = description.replace(reg2, "");

            const tf = confirm("   ?");

            if (tf) {
                try {
                    const {
                        data: { updatePost }
                    } = await upd({
                        variables: {
                            id: post.id,
                            title: title.value,
                            description,
                            content,
                            category: category.value,
                            thumbnail
                        }
                    });
                    if (updatePost) {
                        alert(" .");
                    }
                } catch (error) {
                    const { message, status } = JSON.parse(error.message);
                    if (status === 401) {
                        /**
                         *   
                         */
                        dispatch({
                            type: SHOW_LOGIN_MODAL
                        });
                    } else {
                        alert(message);
                    }
                }
            }
        } else {
            /**
             *   
             */
            dispatch({
                type: SHOW_LOGIN_MODAL
            });
        }
    }, [updatePostLoading, title.value, category.value, content]);

    /**
     *    
     */
    useEffect(() => {
        if (data && data.post) {
            const { post } = data;
            title.setValue(post.title);
            category.setValue(post.category);
            setContent(post.content);
        }
    }, [data]);

    if (!data) {
        return <Loader />;
    }

    const { post } = data;

    return (
        <Container>
            {updatePostLoading && <Loader />}
            <CategoryWrapper>
                <FormInput
                    type="text"
                    placeholder=" "
                    name="category"
                    autoComplete="off"
                    required
                    label=""
                    {...category}
                />
            </CategoryWrapper>
            <FormInput
                type="text"
                placeholder=" "
                name="title"
                autoComplete="off"
                required
                label=""
                {...title}
            />
            <Editor
                onChange={(content) => setContent(content)}
                initialValue={post.content}
                initialEditType="markdown"
            />
            <SubmitWrapper>
                <Button onClick={handleSubmit}></Button>
            </SubmitWrapper>
        </Container>
    );
};

export default UpdatePostPage;
