import React, { useState, useCallback, useEffect } from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import { useQuery, useMutation } from "@apollo/client";
import { GET_POST } from "../../graphql/query/post";
import { DELETE_POST, LIKE_POST } from "../../graphql/mutation/post";
import { useDispatch, useSelector } from "../../context";
import { SHOW_LOGIN_MODAL } from "../../context/action";
import Avatar from "../../components/Avatar";
import BtnLink from "../../components/BtnLink";
import { HeartEmpty, HeartFull } from "../../assets/icon";
import { TOKEN_KEY, getStorage } from "../../lib/state";
import CommentList from "../../components/CommentList";
import Loader from "../../components/Loader";
import Meta from "../../components/Meta";
import Subject from "../../components/Subject";
import { timeForToday } from "../../lib/date";
import Viewer from "../../components/Viewer";

const InfoWrapper = styled.div`
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const Column = styled.div`
    display: flex;
    flex-direction: row;
    & > * {
        margin-right: 10px;
    }
`;

const IconWrapper = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & > * {
        margin-right: 5px;
    }
`;

/**
 *    
 *
 * @Nextpage
 * @author frist
 */
const Post = ({
    match: {
        params: { id }
    }
}) => {
    /**
     * history  
     */
    const history = useHistory();
    /**
     *     
     */
    const dispatch = useDispatch();
    /**
     *     
     */
    const { id: userId } = useSelector();
    /**
     *   
     */
    const { data } = useQuery(GET_POST, {
        variables: {
            id
        }
    });

    /**
     *  mutation 
     */
    const [like] = useMutation(LIKE_POST);
    /**
     *   mutation 
     */
    const [del, { loading: deleteLoading }] = useMutation(DELETE_POST);
    /**
     *      
     */
    const [isLike, setIsLike] = useState(false);
    /**
     *      
     */
    const [likeCount, setLikeCount] = useState(false);
    /**
     *  
     */
    const handleLike = useCallback(async () => {
        /**
         *  
         */
        const token = getStorage(TOKEN_KEY);

        if (token) {
            /**
             *    
             */
            setIsLike(!isLike);
            /**
             *    
             */
            setLikeCount(isLike ? likeCount - 1 : likeCount + 1);

            try {
                await like({
                    variables: { id }
                });
            } catch (error) {
                const { message } = JSON.parse(error.message);
                alert(message);
            }
        } else {
            /**
             *   
             */
            dispatch({
                type: SHOW_LOGIN_MODAL
            });
        }
    }, [isLike, likeCount]);
    /**
     *   
     */
    const handleDelete = useCallback(async () => {
        /**
         *    
         */
        if (deleteLoading) {
            return alert(".  .");
        }

        const tf = confirm(" ?");

        if (tf) {
            try {
                const {
                    data: { deletePost }
                } = await del({
                    variables: { id }
                });

                if (deletePost) {
                    alert(" .");
                    /**
                     *   
                     */
                    history.push("/");
                }
            } catch (error) {
                const { message } = JSON.parse(error.message);
                alert(message);
            }
        }
    }, [deleteLoading]);
    /**
     *   
     */
    const handleUpdate = useCallback(() => {
        const tf = confirm("  ?");
        if (tf) {
            history.push(`/update_post/${id}`);
        }
    }, []);

    /**
     *    
     */
    useEffect(() => {
        if (data && data.post) {
            const { likes } = data.post;
            /**
             *    
             */
            const isLikePost = likes.some((like) => like.user.id === userId);
            /**
             *    
             */
            setIsLike(isLikePost);
            /**
             *    
             */
            setLikeCount(likes.length);
        }
    }, [data && data.post, userId]);

    if (!data) {
        return <Loader />;
    }

    const {
        title,
        description,
        user,
        createdAt,
        content,
        category,
        viewCount
    } = data.post;
    /**
     *   
     */
    const isMyPost = userId ? userId === user.id : false;

    return (
        <div>
            {deleteLoading && <Loader />}
            <Meta title={`Frisklog - ${title}`} description={description} />
            <Subject>{title}</Subject>
            <InfoWrapper>
                <Column>
                    <Avatar src={user.avatar.url} size="30" userId={user.id} />
                    <span>{user.nickname}</span>
                    <span></span>
                    <span>{timeForToday(createdAt)}</span>
                </Column>
            </InfoWrapper>
            <InfoWrapper>
                <Column>
                    <IconWrapper>
                        <BtnLink to={`/category/${category}`}>
                            {category}
                        </BtnLink>
                    </IconWrapper>
                    <IconWrapper>
                        <button
                            type="button"
                            aria-pressed={isLike ? true : false}
                            onClick={handleLike}
                        >
                            {isLike ? (
                                <HeartFull style={{ width: 32, height: 32 }} />
                            ) : (
                                <HeartEmpty style={{ width: 32, height: 32 }} />
                            )}
                            <span className="a11y-hidden">
                                {isLike ? " " : " "}
                            </span>
                        </button>
                        <span>{likeCount}</span>
                    </IconWrapper>
                </Column>

                {isMyPost && (
                    <div>
                        <button
                            type="button"
                            className="btn btn-info mr-1"
                            onClick={handleUpdate}
                        >
                            
                        </button>
                        <button
                            type="button"
                            className="btn btn-danger m"
                            onClick={handleDelete}
                        >
                            
                        </button>
                    </div>
                )}
            </InfoWrapper>
            <Viewer initialValue={content} />
            <hr />
            <CommentList />
        </div>
    );
};

export default Post;
